<template>
  <v-container fluid class="pa-0 pa-md-3">
    <v-list class="ly-list">
      <template v-for="(group, index) of groups">
        <v-list-item :key="group[0]">
          <v-row no-gutters>
            <v-col cols="12" md="5">{{ group[1] }}</v-col>
            <v-col cols="11" md="4" offset="1" offset-sm="0">{{ group[4] }}</v-col>
            <v-col cols="10" md="3" offset="2" offset-sm="0" class="text-right">
              <v-btn icon @click.stop="onDeleteClick(group, index)"
                v-if="'100'!==group[0]"
              >
                <v-icon>mdi-delete-forever</v-icon>
              </v-btn>
              <v-btn icon @click="onChangeShow(group, index)">
                <v-icon>mdi-play-circle-outline</v-icon>
              </v-btn>
              <v-btn icon @click="onEditClick(group, index)">
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==groups.length-1"></v-divider>
      </template>
    </v-list>
    <v-btn fab fixed bottom right dark color="pink"
        :disabled="loading" @click="onBtnAddClick">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-dialog v-model="groupForm" max-width="480" persistent>
      <v-card>
        <v-card-title>Group Media Player</v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="formValid" lazy-validation>
            <v-text-field label="Nama group" v-model="formGroupName"
              :rules="nameRules"></v-text-field>
            <v-select :items="tzListIna" label="Zona waktu"
              v-model="formGroupTimezone"></v-select>
            <v-select :items="orientationList" label="Orientasi layar"
              v-model="formGroupOrientation"></v-select>
            <v-slider v-model="formGroupVolume" append-icon="mdi-volume-high"
              prepend-icon="mdi-volume-low"></v-slider>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="groupForm=false" :disabled="loading">Batal</v-btn>
          <v-btn text color="primary" @click="onFormBtnOkClick" :disabled="loading">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogShow" max-width="480" persistent>
      <v-card>
        <v-card-title>Tayangan Untuk Group</v-card-title>
        <v-card-text>
          <v-form ref="formShow" lazy-validation>
            <v-radio-group v-model="formShowOpt" hide-details>
              <v-radio label="Tidak tayang" value="0"></v-radio>
              <v-radio label="Tampilkan tayangan:" value="1"></v-radio>
            </v-radio-group>
            <v-select :items="shows" item-text="name" item-value="id"
              placeholder="Pilih tayangan" v-model="formShowSK"
              :disabled="formShowOpt==='0'" hide-details></v-select>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dialogShow=false" :disabled="loading">Batal</v-btn>
          <v-btn text color="primary" @click="onChangeShowConfirmed" :disabled="btnOkDlgShowDisabled">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog max-width="320" v-model="dlgConfirmDel">
      <v-card>
        <v-card-title>Konfirmasi</v-card-title>
        <v-card-text>
          Hapus {{ groupNameToBeDel }}?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dlgConfirmDel=false" :disabled="loading">Batal</v-btn>
          <v-btn text color="warning" :disabled="loading" @click="deleteGroup">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { API_URL } from '@/config'
import { deleteWithAuth, getWithAuth, postWithAuth } from '@/lib/fetch_utils'

const mapTzDataToForm = {
  'Asia/Jakarta': 'WIB',
  'Asia/Makassar': 'WITA',
  'Asia/Jayapura': 'WIT'
}

const mapTzFormToData = {
  'WIB': 'Asia/Jakarta',
  'WITA': 'Asia/Makassar',
  'WIT': 'Asia/Jayapura'
}

const mapOrientDataToForm = {
  "0": 'Horizontal',
  '90': 'Vertikal 90',
  '270': 'Vertikal 270'
}

const mapOrientFormToData = {
  'Horizontal': 0,
  'Vertikal 90': 90,
  'Vertikal 270': 270
}

export default {
  name: "Groups",
  data: () => ({
    dialogShow: false,
    dlgConfirmDel: false,
    formGroupName: '',
    formGroupOrientation: 'Horizontal',
    formGroupTimezone: '',
    formGroupVolume: 0,
    formMode: 'add',
    formShowOpt: "0",
    formShowSK: '',
    formValid: true,
    groupForm: false,
    groupNameToBeDel: '',
    groups: [],
    orientationList: ['Horizontal', 'Vertikal 90', 'Vertikal 270'],
    shows: [],
    tzListIna: ['WIB', 'WITA', 'WIT'],
    websocket: 0,
    nameRules: [
      v => !!v || 'Nama harus diisi',
      v => v.length >= 2 || 'Nama terlalu pendek'
    ],
    loading: false
  }),
  computed: {
    btnOkDlgShowDisabled(){
      if ((this.formShowOpt === "1") && (this.formShowSK === "")) return true
      if (this.loading) return true
      return false
    }
  },
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    },
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title:"Player Group"})
    this.loadData()
  },
  methods: {
    loadData: async function() {
      let resp = await getWithAuth(`${API_URL}/groups?v=s`)
      if (200 === resp.code) this.groups = resp.data.rows
    },
    getShowNameById(id) {
      for (let i=0; i<this.shows.length; i++) {
        if (id===this.shows[i].id) {
          return this.shows[i].name
        }
      }
      return null
    },
    onBtnAddClick() {
      this.formMode = 'add'
      this.formGroupName = ''
      this.formGroupTimezone = 'WIB'
      this.formGroupOrientation = 'Horizontal'
      this.formGroupVolume = 0
      this.groupForm = true
      let self = this
      this.$nextTick(() => { self.$refs.form.resetValidation() })
    },
    onEditClick: async function(group, index) {
      let resp = await getWithAuth(`${API_URL}/groups/${group[0]}`)
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      let d = resp.data
      this.formMode = 'edit'
      this.formGroupName = group[1]
      this.formGroupTimezone = mapTzDataToForm[d.timezone]
      this.formGroupOrientation = mapOrientDataToForm[d.orientation]
      this.formGroupVolume = d.volume
      //this.websocket = d.websocket
      this.editedGroup = group
      this.editedGroupIndex = index
      this.groupForm = true
      let self = this
      this.$nextTick(() => {self.$refs.form.resetValidation()});
    },
    onFormBtnOkClick() {
      if (!this.$refs.form.validate()) return
      if (this.formMode === 'add') {
        this.addGroup()
      } else {
        this.updateGroup()
      }
    },
    onChangeShow: async function(group, index) {
      if (this.shows.length===0) {
        let resp = await getWithAuth(`${API_URL}/shows`)
        if (200!== resp.code) {
          return
        }
        this.shows = []
        let self = this
        resp.data.rows.forEach(show => {
          self.shows.push(
            { id: show[0], name: show[1] }
          )
        })
      }
      this.editedGroup = group
      this.editedGroupIndex = index
      this.formShowOpt = "0"
      this.formShowSK = ""
      if (group[3]!==null) {
        this.formShowOpt = "1"
        this.formShowSK = group[3]
      }
      this.dialogShow = true
    },
    onDeleteClick(group, index) {
      this.groupNameToBeDel = group[1]
      this.groupIndexToBeDel = index
      this.dlgConfirmDel = true
    },
    addGroup: async function() {
      let data = {
        name: this.formGroupName,
        orientation: mapOrientFormToData[this.formGroupOrientation],
        volume: this.formGroupVolume,
        timezone: mapTzFormToData[this.formGroupTimezone],
        websocket: this.websocket // NOTE: Disabled feature
      }
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/groups`, data)
      this.loading = false
      this.groupForm = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      let nowTs = Math.round((new Date())/1000)
      this.groups.unshift([resp.data, this.formGroupName, nowTs])
    },
    updateGroup: async function() {
      let data = {
        name: this.formGroupName,
        orientation: mapOrientFormToData[this.formGroupOrientation],
        volume: this.formGroupVolume,
        timezone: mapTzFormToData[this.formGroupTimezone],
        websocket: this.websocket // NOTE: Disabled feature
      }
      let groupId = this.editedGroup[0]
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/groups/${groupId}`, data)
      this.loading = false
      this.groupForm = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.editedGroup[1] = this.formGroupName
    },
    onChangeShowConfirmed: async function() {
      let resp = null
      let editedGroup = this.groups[this.editedGroupIndex]
      let groupId = editedGroup[0]
      this.loading = true
      if (this.formShowOpt === "0") {
        resp = await deleteWithAuth(`${API_URL}/group-show/${groupId}`)
      } else {
        if (this.formShowSK === '') {
          this.$store.commit('SHOW_APP_ALERT', {
            title: 'Error',
            text: 'Anda harus memilih tayangan!'
          })
          return
        }
        let params = { show_id: this.formShowSK }
        resp = await postWithAuth(`${API_URL}/group-show/${groupId}`, params)
      }
      this.loading = false
      this.dialogShow = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      if (this.formShowOpt === "0") {
        editedGroup[3] = null
        editedGroup[4] = null
      } else {
        editedGroup[3] = this.formShowSK
        editedGroup[4] = this.getShowNameById(this.formShowSK)
      }
    },
    deleteGroup: async function() {
      let goupId = this.groups[this.groupIndexToBeDel][0]
      this.loading = true
      let resp = await deleteWithAuth(`${API_URL}/groups/${goupId}`)
      this.dlgConfirmDel = false
      this.loading = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.groups.splice(this.groupIndexToBeDel, 1)
    },
  }
}
</script>

<style scoped>
.ly-list {
  margin-bottom: 64px;
}
</style>
